import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameMediator } from "../Mediator/GameMediator";
import { No } from "../Component/No";
import { BackGroundGroup } from "../Component/BackGroundGroup";
import { ObjGroup } from "../Component/ObjGroup";
import { TimerGroup } from "../Component/TimerGroup";
import { Commercial } from "../Component/Commercial";
import { TweenMax, Power1 } from "gsap/TweenMax";
import { ShapComponent } from "../Component/ShapeComponent";
import { GameCommandEvent } from "../../Event/GameEvent";
import { SoundBtn } from "../Component/SoundBtn";
import { Issue } from "../Component/Issue";

export class GamePanel extends PIXI.Container {
    constructor(data) {
        super();

        this._multitonKey = data.key;
        this._stageWidth = data.width;
        this._stageHeight = data.height;

        this.addListener("added", this.onAddToStage);

    }

    onAddToStage() {
        ApplicationFacade.getInstance(this._multitonKey).registerMediator(new GameMediator(this));

        this._backgroundgroupComponent = new BackGroundGroup();
        this.addChild(this._backgroundgroupComponent);
        this._backgroundgroupComponent.y = 55;

        // this._commercialComponent = new Commercial();
        // this.addChild(this._commercialComponent);
        // this._commercialComponent.x = 335;
        // this._commercialComponent.y = 115;

        this._objGroupComponent = new ObjGroup();
        this.addChild(this._objGroupComponent);

        this._timerGroupComponent = new TimerGroup();
        this.addChild(this._timerGroupComponent);
        this._timerGroupComponent.x = this._stageWidth * 0.5;
        this._timerGroupComponent.y = this._stageHeight * 0.5;

        this._currNoComponent = new No();
        this.addChild(this._currNoComponent);
        this._currNoComponent.x = this._stageWidth * 0.5;
        this._currNoComponent.y = 5;

        this._preNoTitle = new PIXI.Text("",
            {
                fontFamily: 'Arial',
                fontSize: 24,
                // fontStyle: 'italic',
                fontWeight: 'bold',
                fill: '#ffffff',
            });
        this.addChild(this._preNoTitle);
        this._preNoTitle.x = this._stageWidth * 0.3;
        this._preNoTitle.anchor.set(0, 0.5);
        this._preNoTitle.y = this._stageHeight - 30;

        this._preNoComponent = new No();
        this.addChild(this._preNoComponent);
        this._preNoComponent.x = this._stageWidth * 0.7;
        this._preNoComponent.y = this._stageHeight - 50;

        // this._issue = new Issue();
        // this.addChild(this._issue);

        // this._gameTitle = new PIXI.Text("(PK10)",
        //     {
        //         fontFamily: 'Arial',
        //         fontSize: 20,
        //         // fontStyle: 'italic',
        //         fontWeight: 'bold',
        //         fill: '#ffffff',
        //     });
        // this.addChild(this._gameTitle);

        this._currIssueTitle = new PIXI.Text(":",
            {
                fontFamily: 'Arial',
                fontSize: 22,
                // fontStyle: 'italic',
                fontWeight: 'bold',
                fill: ['#FFFF00']
            });
        this._currIssueTitle.anchor.set(0, 0.5);
        this._currIssueTitle.position.set(0, 24);
        this.addChild(this._currIssueTitle);

        this._currIssue = new PIXI.Text("123456789",
            {
                fontFamily: 'Arial',
                fontSize: 22,
                // fontStyle: 'italic',
                // fontWeight: 'bold',
                fill: ['#FFFF00']
            });
        this._currIssue.anchor.set(0, 0.5);
        this._currIssue.position.set(this._currIssueTitle.width, 25);
        this.addChild(this._currIssue);

        this._preIssue = new PIXI.Text(":123456789",
            {
                fontFamily: 'Arial',
                fontSize: 22,
                // fontStyle: 'italic',
                fontWeight: 'bold',
                fill: ['#ffffff']
            });
        this._preIssue.anchor.set(0, 0.5);
        this._preIssue.position.set(5, this._stageHeight - 30);
        this.addChild(this._preIssue);

        this._soundbtnComponent = new SoundBtn();
        this.addChild(this._soundbtnComponent);

        // this._closeBtn = new PIXI.Sprite();
        // this.addChild(this._closeBtn);

        this._shapeComponent = new ShapComponent(0xffffff, 1, this._stageWidth, this._stageHeight);
        this.addChild(this._shapeComponent);
        this._shapeComponent.visible = false;
    }

    init(data) {
        this._backgroundgroupComponent.init(data.bg);
        this._objGroupComponent.init(data.objs);
        // this._commercialComponent.init(data.commerical);
        this._timerGroupComponent.init(data.timer);
        this._currNoComponent.init(data.no);
        this._preNoComponent.init(data.no);
        this._preNoComponent.upPreRank(data.preNum);
        // this._issue.init();
        this._soundbtnComponent.init(data.sound);

        // this._closeBtn.texture = data.closeBtn;
        // this._closeBtn.anchor.set(0, 0);
        // this._closeBtn.scale.set(1.2);
        // this._closeBtn.interactive = true;
        // this._closeBtn.buttonMode = true;
        // this._closeBtn.position.set(this.StageWidth - this._closeBtn.width - 55, 3.5);
        // this._closeBtn.on("pointerdown", () => {
        //     ApplicationFacade.getInstance(this._multitonKey).sendNotification(GameCommandEvent.CLOSEBTN);
        // });

        ApplicationFacade.getInstance(this._multitonKey).sendNotification(GameCommandEvent.INITCOMPLETE);
    }

    reset() {
        this._shapeComponent.visible = false;
        this._shapeComponent.alpha = 1;

        // this._commercialComponent.alpha = 1;
        // this._commercialComponent.x = 335;
        // this._commercialComponent.reset();

        this._currNoComponent.reset();
        this._objGroupComponent.reset();
        this._timerGroupComponent.reset();
        this._backgroundgroupComponent.reset();
    }

    gameStart() {
        // if (this._commercialComponent != null)
        // {
        //     TweenMax.to(this._commercialComponent, 1,
        //         {
        //             x: this._stageWidth * 1.5,
        //             alpha: 0,
        //             onComplete: () =>
        //             {
        //                 this._commercialComponent.pause();
        //                 this._commercialComponent.visible = false;
        //             }
        //         });
        // }

        this._backgroundgroupComponent.gameStart();
    }

    flashLight(time) {
        this._shapeComponent.visible = true;
        TweenMax.to(this._shapeComponent, time,
            {
                alpha: 0,
                ease: Power1.easeInOut,
                onComplete: () => {
                    ApplicationFacade.getInstance(this._multitonKey).sendNotification(GameCommandEvent.FLASHLIGHTFINSH);
                    this._shapeComponent.visible = false;
                    this._shapeComponent.alpha = 1;
                }
            });
    }

    UpIssue(data) {
        this._currIssue.text = "" + data.Curr;
        this._preIssue.text = ":" + data.Pre;
    }

    get soundbtnComponent() {
        return this._soundbtnComponent
    }

    get ObjGroup() {
        return this._objGroupComponent;
    }

    get MultitonKey() {
        return this._multitonKey;
    }

    get StageWidth() {
        return this._stageWidth;
    }

    get StageHeight() {
        return this._stageHeight;
    }

    get Issue() {
        return this._issue;
    }

    get CurrNoComponent() {
        return this._currNoComponent;
    }

    get PreNoComponent() {
        return this._preNoComponent;
    }

    get CommercialComponent() {
        return this._commercialComponent;
    }

    get TimerGroupComponent() {
        return this._timerGroupComponent;
    }

    get BackgroundgroupComponent() {
        return this._backgroundgroupComponent;
    }
}