import * as PIXI from "pixi.js";

export class ShapComponent extends PIXI.Container
{
    // _color;
    // _alpha;
    // _shape;
    // _width;
    // _height;

    constructor(color, alpha, width, height)
    {
        super();

        this._color = color;
        this._alpha = alpha;
        this._width = width;
        this._height = height;

        this.once("added", this.onAddToStage);
    }

    onAddToStage ()
    {
        this._shape = new PIXI.Graphics();
        this._shape.beginFill(this._color, this._alpha);
        this._shape.drawRect(0, 0, this._width, this._height);
        this._shape.endFill();
        this.addChild(this._shape);
    }
}