import * as PIXI from "pixi.js";
import { Lerp, Transition, Style } from "../../../Tools/Lerp/Lerp";
import { MoveInfo } from "../../Other/MoveInfo";
import { GameProxyEvent } from "../../Event/GameEvent";

export class Obj
{
    // _id;
    // _no;
    // _pos;
    // _orignPos;
    // _scale;
    // _animaFrame;
    // _preMoveInfo;
    // _objLerp;
    // _gameProxy;

    constructor(id, no, pos, scale, frame, gameproxy)
    {
        this._id = id;
        this._no = no;
        this._pos = new PIXI.Point(pos.x, pos.y);
        this._orignPos = new PIXI.Point(pos.x, pos.y);
        this._scale = scale;
        this._animaFrame = frame;
        this._gameProxy = gameproxy;
        this._isGoal = false;
        this._preMoveInfo = new MoveInfo(0, 0);

        this._objLerp = new Lerp();
    }

    reset ()
    {
        this._isGoal = false;

        this._pos.x = this._orignPos.x;
        this._pos.y = this._orignPos.y;
        this.stopLerp()

        this._gameProxy.sendNotification(GameProxyEvent.UPDATEOBJ, {
            "Id": this._id,
            "pos": this._pos,
            "animaFrame": this._animaFrame,
            "frameSpeed": 0.3 + Math.random() * 0.05
        });
    }

    move ()
    {
        this._pos.x -= this._preMoveInfo.lastSpeed;

        this._gameProxy.sendNotification(GameProxyEvent.UPDATEOBJ, {
            "Id": this._id,
            "pos": this._pos,
            "animaFrame": this._animaFrame,
            "frameSpeed": 0.4 + Math.random() * 0.05
        });
    }

    lerp (moveinfo)
    {
        //console.log( this._pos.x + "  to   " + ( moveinfo.MoveXPer * 884 / 100 ) + "   " + moveinfo.Time );

        // if (!this._gameProxy.IsFinshGame)
        // {
        //     this._objLerp.define([[this._pos.x, moveinfo.MoveXPer * this._gameProxy.StageWidth / 100]]
        //         , moveinfo.Time, Transition.EaseInOut, Style.Linear);
        // }
        // else
        // {
        //     this._objLerp.define([[this._pos.x, moveinfo.MoveXPer * (1 - 0.06 * (this._no - 1)) * this._gameProxy.StageWidth / 100]]
        //         , moveinfo.Time, Transition.EaseInOut, Style.Linear);
        // }

        let newx = moveinfo.MoveXPer * this._gameProxy.StageWidth / 100 - (1.00 - (this._scale.x + this._scale.y) * 0.5) * 282;
        this._objLerp.define([[this._pos.x, newx]], moveinfo.Time, Transition.EaseInOut, Style.Linear);


        let dis = moveinfo.MoveXPer - this._preMoveInfo.MoveXPer;
        this._objLerp.lerp((values, timer) =>
        {
            // console.log( this._pos.x + " " + values[ 0 ] + "   " + ( values[ 0 ] - this._pos.x ) );
            this._pos.x = values[0];
            this._gameProxy.sendNotification(GameProxyEvent.UPDATEOBJ, {
                "Id": this._id,
                "pos": this._pos,
                "animaFrame": this._animaFrame,
                "frameRate": this.conventFrameRate(dis)
            });
        });

        this._preMoveInfo = moveinfo;
    }

    stopLerp ()
    {
        this._objLerp.stop();
    }

    conventFrameRate (disxPer)
    {
        if (disxPer < 0)
        {
            return 0.2 + Math.random() * 0.05;
        }
        else if (disxPer > 10)
        {
            return 0.4 + Math.random() * 0.05;
        }
        else return 0.3 + Math.random() * 0.05;
    }

    get Id ()
    {
        return this._id;
    }

    get No ()
    {
        return this._no;
    }

    set No (v)
    {
        this._no = v;
    }

    get Pos ()
    {
        return this._pos;
    }

    set Pos (v)
    {
        return this._pos = v;
    }

    get OringPos ()
    {
        return this._orignPos;
    }

    get Scale ()
    {
        return this._scale;
    }

    get MovieKey ()
    {
        return this._movieKey;
    }

    set AnimaFrame (value)
    {
        this._animaFrame = value;
    }

    get AnimaFrame ()
    {
        return this._animaFrame;
    }

    get PreMoveInfo ()
    {
        return this._preMoveInfo;
    }

    get IsGoal ()
    {
        return this._isGoal;
    }
    set IsGoal (v)
    {
        this._isGoal = v;
    }
}
