import MVC from "puremvc";
import * as PIXI from "pixi.js";
import { GameProxyEvent } from "../../Event/GameEvent";
import { GameConfig } from "../Data/GameConfig";
import { Obj } from "../Obj/Obj";
import { Map } from "../../../Tools/Map";
import { ObjFrame } from "../../Other/ObjFrame";
import { CustomRandom } from "../../../Tools/CustomRandom";
import { RankInfo } from "../../Other/RankInfo";
import { MoveInfo } from "../../Other/MoveInfo";
import { AudioCommandEvent } from "../../Event/AudioEvent";
import { AudioManifest } from "../../Other/AudioManifest";
import { Tool } from "../../../Tools/Tool";
// import { ApplicationFacadeEvent } from "../../Event/ApplicationFacadeEvent";

export class GameProxy extends MVC.Proxy {
    static get NAME() {
        return "GameProxy";
    }

    constructor(key) {
        super(GameProxy.NAME);

        this.initializeNotifier(key);

        this._issue = 0;

        this._isCustSeed = false;
        // _customRandom;

        this._currSectionCount = 0;
        this._currCountTimer = 0;
        this._currSectionTimer = 0;
        this._isStartCountDown = false;

        this._finshTime = [];
        this._curNum = [];
        this._preNum = [];
        this._onClickCloseCallBack = null;
        this._readyCallBack = undefined;
        this._runStartCallBack = null;
        this._finshLineCallBack = undefined;
        this._finshGameCallBack = undefined;

        this._moveinfos_foward = [];
        this._moveinfos_back = [];
        this._finshMoveInfoMap_map;

        this._isGoal = false;
        this._isStartGame = false;
        this._isFinshGame = false;
        this._isLastSection = false;
        this._isMovePause = false;

        this._finshStartTime = 0;

        this.getBgInfo = this.getBgInfo.bind(this)
    }

    onRemove() {
        this._isRemove = true
        this._ticker.destroy();

        for (let i = 0; i < this._objs.length; i++) {
            this._objs.element(i).Value.stopLerp();
        }
    }

    //
    async init(data) {
        this._readyCallBack = data.onReady;
        this._runStartCallBack = data.onStartGame;
        this._finshLineCallBack = data.onFinshLine;
        this._finshGameCallBack = data.onFinshGame;
        this._onClickCloseCallBack = data.onClose;

        this._stageWidth = data.width;
        this._stageHeight = data.height;
        this._assetRootSrc = data.assetRootSrc;

        this._objs = new Map();

        this._customRandom = new CustomRandom(Math.random() * 100 * Math.random() * 100);

        this._ticker = new PIXI.ticker.Ticker();
        this._ticker.add(() => this.update());

        this._bgInfo = data.loader.resources["bgjson"].data;
        let background = await this.getBackGround(data.background);

        let objs = this.getObjs();

        var noSprites = [];
        for (let i = 0; i < 10; i++) {
            noSprites.push(PIXI.Texture.fromFrame('NO_' + Tool.PadLeft((i + 1), 2, "0") + '.png'));
        }

        this.sendNotification(GameProxyEvent.INITGAMEPANEL,
            {
                bg: background,
                objs: objs,
                preNum: data.preNum,
                commerical:
                {
                    // "cm": PIXI.Texture.fromFrame("BG_CM_01.png"),
                    "board": PIXI.Texture.fromFrame("BG_CM_board_upper.png")
                },
                timer:
                {
                    "standbybg": PIXI.Texture.fromFrame("standby_count_plate.png"),
                    "startbg": PIXI.Texture.fromFrame("start_BG.png"),
                    "ligthBbg": PIXI.Texture.fromFrame("start_signal_B.png"),
                    "lightGbg": PIXI.Texture.fromFrame("start_signal_G.png"),
                    "ligthRbg": PIXI.Texture.fromFrame("start_signal_R.png"),
                },
                no: noSprites,
                sound:
                {
                    isMuteAll: data.isMuteAll,
                    "enable": PIXI.Texture.fromFrame("sound_on.png"),
                    "disable": PIXI.Texture.fromFrame("sound_of.png"),
                },
                closeBtn: PIXI.Texture.fromFrame("close_btn.png"),
            });


        let rewardCars = [];

        for (let i = 0; i < 10; i++) {
            rewardCars.push(PIXI.Texture.fromFrame(Tool.Format("car{0}_reward.png", Tool.PadLeft((i + 1), 2, "0"))));
        }

        this.sendNotification(GameProxyEvent.INITREWARDPANEL,
            {
                "bg": PIXI.Texture.fromFrame("reward_BG.png"),
                "rd1": PIXI.Texture.fromFrame("reward_1st.png"),
                "rd2": PIXI.Texture.fromFrame("reward_2nd.png"),
                "rd3": PIXI.Texture.fromFrame("reward_3rd.png"),
                // "flash": PIXI.Texture.fromFrame("reward_flash.png"),
                "rewardObjTextures": rewardCars
            });

        this.setIssue({ currIssue: data.currIssue, preIssue: data.preIssue });

        this.sendNotification(AudioCommandEvent.PLAY, { "key": AudioManifest.car_stanby, "loop": true });
    }

    async getBackGround(key = 0) {
        let bginfo = this.getBgInfo(key);

        if (bginfo != null) {
            let racebg, streetbg;
            let imgkeys = [];

            let goalbg = PIXI.Texture.fromFrame("start_goal.png");

            let raceKey = bginfo.racebg + ".png";
            if (PIXI.utils.TextureCache[raceKey] === undefined) {
                imgkeys.push({ type: "race", key: raceKey });
            }
            else racebg = PIXI.Texture.fromFrame(raceKey);

            let streetKey = bginfo.streetbg + ".png";
            if (PIXI.utils.TextureCache[streetKey] === undefined) {
                imgkeys.push({ type: "stree", key: streetKey });
            }
            else streetbg = PIXI.Texture.fromFrame(streetKey);

            let res = await this.AsyncLoadLocalImages(imgkeys);

            for (let i = 0; i < imgkeys.length; i++) {
                switch (imgkeys[i].type) {
                    case "race":
                        racebg = res[imgkeys[i].key].texture;
                        break;
                    case "stree":
                        streetbg = res[imgkeys[i].key].texture;
                        break;
                    default:
                        break;
                }
            }

            return {
                "bgCount": GameConfig.BackGroundCount,
                "goalPosXPer": GameConfig.EndPointPercent,
                "bgInterval": GameConfig.BackGroundXInterval,
                "auditoriumCount": GameConfig.AuditoriumCount,
                "auditoriumInterval": GameConfig.AuditoriumXInterval,
                "racebg": racebg,
                "streetbg": streetbg,
                "goalbg": goalbg,
            }
        }
        else console.log("GameProxy initBackGround bginfo is null");
    }

    getObjs() {
        let objs = [];
        for (let i = 0; i < 10; i++) {
            let pos;

            if (i === 0) {
                pos = new PIXI.Point(547, 160);
                // pos = new PIXI.Point(144, 163);
            }
            else {
                pos = new PIXI.Point(this._objs.getValue(GameConfig.MovieKey + (i - 1)).Pos.x + 10 + 0.5 * (i - 1),
                    this._objs.getValue(GameConfig.MovieKey + (i - 1)).Pos.y + 30 + 1 * (i - 1));
                // pos = new PIXI.Point(this._objs.getValue(GameConfig.MovieKey + (i - 1)).Pos.x - 25 - 0.4 * (i - 1),
                //     this._objs.getValue(GameConfig.MovieKey + (i - 1)).Pos.y + 30 + 1 * (i - 1));
            }

            let scale = new PIXI.Point(0.55 + i * 0.05, 0.55 + i * 0.05);
            let idleTextures = [
                PIXI.Texture.fromFrame(Tool.Format("car{0}_run{1}.png", Tool.PadLeft((i + 1), 2, "0"), Tool.PadLeft(0, 3, "0")))];

            let run1Textures = [];
            for (let x = 0; x < 2; x++) {
                run1Textures.push(
                    PIXI.Texture.fromFrame(Tool.Format("car{0}_run{1}.png", Tool.PadLeft((i + 1), 2, "0"), Tool.PadLeft(x, 3, "0")))
                )
            }

            let run2Textures = [];
            for (let x = 0; x < 2; x++) {
                run2Textures.push(
                    PIXI.Texture.fromFrame(Tool.Format("car{0}_runfast{1}.png", Tool.PadLeft((i + 1), 2, "0"), Tool.PadLeft(x, 3, "0"))));
            }

            let tempObj = new Obj(GameConfig.MovieKey + i, i, pos, scale, ObjFrame.Idle, this);
            this._objs.push(tempObj._id, tempObj);

            objs.push({
                "Id": tempObj.Id,
                "animaFrame": tempObj.AnimaFrame,
                "pos": tempObj.Pos,
                "scale": tempObj.Scale,
                "textures":
                {
                    "Idle": idleTextures,
                    "Run1": run1Textures,
                    "Run2": run2Textures,
                }
            });

        }

        return objs;
    }

    initcomplete() {
        if (this._readyCallBack !== undefined) {
            this._readyCallBack();
        }
    }

    reset(data = null) {

        // console.log("Use Total Time:" + (this._ticker.lastTime - this._finshStartTime));
        // console.log("Use RewardTotal Time:" + (this._ticker.lastTime - this._rankStartTime));

        this._isMovePause = false;
        this._isGoal = false;
        this._isCustSeed = false;
        this._isStartGame = false;
        this._isFinshGame = false;
        this._isLastSection = false;
        this._currSectionCount = 0;
        this._currSectionTimer = 0;

        this._moveinfos_foward = [];
        this._moveinfos_back = [];
        this._finshTime = [];
        this._finshStartTime = 0;

        for (let i = 0; i < this._objs.length; i++) {
            this._objs.element(i).Value.AnimaFrame = ObjFrame.Idle;
            this._objs.element(i).Value.reset();
        }

        this.sendNotification(GameProxyEvent.RESET);

        if (data != null) {
            this._currIssue = data.currIssue;
            this._preIssue = data.preIssue;

            this.setIssue({ currIssue: data.currIssue, preIssue: data.preIssue });
            this.setBackGround(data.background);
            this.setPreRank(data.preNum);
        }


        this.sendNotification(AudioCommandEvent.PLAY, { "key": AudioManifest.car_stanby, "loop": true });
    }

    update() {
        if (this._isStartCountDown) {
            let mathTime = Math.floor(this._currCountTimer);
            if (mathTime > 0) {
                this._currCountTimer -= this._ticker.elapsedMS / 1000;
                this.sendNotification(GameProxyEvent.UPDATETIME, mathTime);
                if (mathTime === 3) {
                    this.sendNotification(AudioCommandEvent.PLAY, { "key": AudioManifest.reciprocaltime, "plural": false });
                }
            }
            else {
                this.sendNotification(GameProxyEvent.UPDATETIME, 0);
                setTimeout(() => this.sendNotification(GameProxyEvent.CLOSETIMER), 500);
                this.startGame();
            }
        }

        if (this._isStartGame && !this.checkbool) {
            this.sendNotification(GameProxyEvent.UPDATEBACKGROUND);

            if (!this._isFinshGame) {
                if (this._currSectionTimer <= 0) {
                    this.changeObjSepeed();
                    this._currSectionTimer = GameConfig.SectionTime + GameConfig.SectionTime * this._customRandom.nextDouble();
                }
                else this._currSectionTimer -= this._ticker.elapsedMS / 1000;
            }
            else if (this._isFinshGame && !this._isLastSection) {
                if (this._currSectionTimer <= 0) {
                    this.changeObjSepeed();
                    this._currSectionTimer = this._finshTime[GameConfig.InsertFinsSection - this._currSectionCount];
                }
                else this._currSectionTimer -= this._ticker.elapsedMS / 1000;
            }
            else if (this._isLastSection && !this._isMovePause) {
                for (let i = 0; i < this._objs.length; i++) {
                    let tempObj = this._objs.Elements[i].Value;
                    tempObj.move();

                    if (this._isGoal) {
                        if (!tempObj._isGoal && tempObj.Pos.x < tempObj.OringPos.x - 410)//410
                        {
                            tempObj.IsGoal = true;

                            if (this._curNum[2] === (tempObj.No + 1)) {
                                this.flashLight();
                                //this.checkbool = true;
                            }

                            if (this._curNum[9] === (tempObj.No + 1)) {
                                this.showRank();
                            }
                        }

                        if (tempObj.Pos.x < -this.StageWidth && tempObj.AnimaFrame !== ObjFrame.Idle) {
                            tempObj.AnimaFrame = ObjFrame.Idle;
                        }
                    }
                }
            }

            this.rankSort(this._objs.Values.map(s => new RankInfo(s.No, s.Pos.x, s.OringPos.x, s.Scale)));

        }
    }

    //
    rankSort(rankinfo) {
        let tempNumbers = rankinfo;
        if (this._isFinshGame) {
            for (let i = 0; i < this._objs.length; i++) {
                for (let j = 0; j < tempNumbers.length - 1; j++) {
                    // if (tempNumbers[j].PosX - tempNumbers[j].OrignX - 432 < tempNumbers[j + 1].PosX - tempNumbers[j + 1].OrignX - 432 && tempNumbers[j].PosX < tempNumbers[j + 1].PosX)
                    // if (tempNumbers[j].PosX + 282 * tempNumbers[j].Scale.x - 411 > tempNumbers[j + 1].PosX + 282 * tempNumbers[j + 1].Scale.x - 411)
                    if (tempNumbers[j].PosX - tempNumbers[j].OrignX - 282 * tempNumbers[j].Scale.x > tempNumbers[j + 1].PosX - tempNumbers[j + 1].OrignX - 282 * tempNumbers[j].Scale.x) {
                        let tempNum = tempNumbers[j];
                        tempNumbers[j] = tempNumbers[j + 1];
                        tempNumbers[j + 1] = tempNum;
                    }
                }
            }
        }
        else {
            for (let i = 0; i < this._objs.length; i++) {
                for (let j = 0; j < tempNumbers.length - 1; j++) {
                    if (tempNumbers[j].PosX > tempNumbers[j + 1].PosX) {
                        let tempNum = tempNumbers[j];
                        tempNumbers[j] = tempNumbers[j + 1];
                        tempNumbers[j + 1] = tempNum;
                    }
                }
            }
        }

        this.sendNotification(GameProxyEvent.UPDATERANK, tempNumbers);
    }

    //  
    changeObjSepeed() {

        this._currSectionCount++;
        if (!this._isFinshGame) {
            for (let i = 0; i < this._objs.length; i++) {
                let tempobj = this._objs.Elements[i].Value;
                let tempMoveInfo;
                if (this._currSectionCount === 1) {
                    tempobj.AnimaFrame = ObjFrame.Run2;
                    tempMoveInfo = this.randomMoveInfo(tempobj.No, 85 - (5 + Math.random() * 25 + Math.random() * 15));// Math.floor( tempobj.Pos.x / this.StageWidth * 100 )
                }
                else {
                    tempMoveInfo = this.randomMoveInfo(tempobj.No, tempobj.PreMoveInfo.MoveXPer);

                    if (tempMoveInfo.MoveXPer - tempobj.PreMoveInfo.MoveXPer > 0) {
                        tempobj.AnimaFrame = ObjFrame.Run1;
                    }
                    else if (tempMoveInfo.MoveXPer - tempobj.PreMoveInfo.MoveXPer < -5) {
                        tempobj.AnimaFrame = ObjFrame.Run2;
                    }
                    else {
                        tempobj.AnimaFrame = ObjFrame.Run1;
                    }
                }
                tempobj.lerp(tempMoveInfo);
            }
        }
        else if (this._currSectionCount <= GameConfig.InsertFinsSection) {
            if (this._currSectionCount === GameConfig.InsertFinsSection) {
                setTimeout(() => {
                    this.sendNotification(GameProxyEvent.OPENGOAL);
                }, this._finshTime[GameConfig.InsertFinsSection - 1] * 1000);
            }

            for (let i = 0; i < this._objs.length; i++) {
                let tempobj = this._objs.Elements[i].Value;
                tempobj.AnimaFrame = ObjFrame.Run2;
                tempobj.lerp(this._finshMoveInfoMap_map.getValue(tempobj.Id).getValue(this._currSectionCount));
            }
        }

    }

    //
    randomMoveInfo(no, currPer, rand = this._customRandom.nextDouble(no)) {
        let moveinfo = rand > 0.8 ? this._customRandom.pickInt(this._moveinfos_foward) : this._customRandom.pickInt(this._moveinfos_back);

        if (currPer + moveinfo.MoveXPer < GameConfig.MaxMoveXPercent) {
            moveinfo = this._customRandom.pickInt(this._moveinfos_back);
            // return this.randomMoveInfo(no, currPer, 0);
        }
        else if (currPer + moveinfo.MoveXPer > GameConfig.MinMoveXPercent) {
            moveinfo = this._customRandom.pickInt(this._moveinfos_foward);
            // return this.randomMoveInfo(no, currPer, 1);
        }

        return new MoveInfo(moveinfo.Time, currPer + moveinfo.MoveXPer);
    }

    // 3  
    flashLight() {
        this._isMovePause = true;

        this.sendNotification(AudioCommandEvent.PAUSEALL);

        this.sendNotification(GameProxyEvent.OBJMOVIECLIPPAUES, true);

        this.sendNotification(GameProxyEvent.FlASHLIGHT, GameConfig.FlashTime / 1000);
    }

    //
    flashLightFinsh() {
        this._isMovePause = false;

        this.sendNotification(GameProxyEvent.OBJMOVIECLIPPAUES, false);

        this.sendNotification(AudioCommandEvent.PAUSEALL);

        if (this._finshLineCallBack) {
            this._finshLineCallBack();
        }
    }

    //123 
    showRank() {
        this.sendNotification(GameProxyEvent.SHOWRANK, {
            "rewardTime": GameConfig.RewardTotalTime,//+ (GameConfig.FinshTotalTime - (this._ticker.lastTime - this._finshStartTime)),
            "first": this._curNum[0],
            "second": this._curNum[1],
            "third": this._curNum[2]
        });

        this._rankStartTime = this._ticker.lastTime;
        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.car_chase);
    }

    goal() {
        this._isGoal = true;
        this._isLastSection = true;

        for (let i = 0; i < this._objs.length; i++) {
            let tempobj = this._objs.Elements[i].Value;
            tempobj.stopLerp();
        }
    }

    finshAnimation() {
        if (this._finshGameCallBack) {
            this._finshGameCallBack();
        }
    }

    startGame() {
        this._isStartCountDown = false;

        if (!this._isCustSeed) {
            this._customRandom.Seed = (this._currIssue + 1);
        }

        for (let i = 0; i < GameConfig.MoveInfoCount; i++) {
            this._moveinfos_foward.push(new MoveInfo(this._customRandom.nextDoubleRange(3, 4), -this._customRandom.nextIntRange(5, 15)));
            this._moveinfos_back.push(new MoveInfo(this._customRandom.nextDoubleRange(2, 3), this._customRandom.nextIntRange(1, 4)));
        }


        this.sendNotification(GameProxyEvent.GAMESTART);

        this._isStartGame = true;

        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.car_stanby);
        this.sendNotification(AudioCommandEvent.PLAY, { "key": AudioManifest.car_chase, "loop": true });

        if (this._runStartCallBack) {
            this._runStartCallBack();
        }
    }

    onCloseBtn() {
        if (this._onClickCloseCallBack) {
            this._onClickCloseCallBack();
        }
    }

    setOnClickCloseCallBack(fn) {
        this._onClickCloseCallBack = fn;
    }

    //
    setReadyCallBack(fn) {
        this._readyCallBack = fn;
    }

    // 3 
    setFinshLineCallBack(fn) {
        this._finshLineCallBack = fn;
    }

    setFinshGameCallBack(fn) {
        this._finshGameCallBack = fn;
    }

    setIssue(data) {
        this._currIssue = data.currIssue;
        this._preIssue = data.preIssue;
        // if (this._currIssue > 0)
        //     this._preIssue = this._currIssue - 1;
        // else this._preIssue = 0;
        this.sendNotification(GameProxyEvent.UPISSUE, { Curr: this._currIssue, Pre: this._preIssue });
    }

    setPreRank(nums) {
        this._preNum = nums;
        this.sendNotification(GameProxyEvent.UPDATEPRERANK, nums);
    }

    //
    async setBackGround(key) {
        let bginfo = this.getBgInfo(key);

        if (bginfo != null) {
            let racebg, streetbg;
            let imgkeys = [];

            let raceKey = bginfo.racebg + ".png";
            if (PIXI.utils.TextureCache[raceKey] === undefined) {
                imgkeys.push({ type: "race", key: raceKey });
            }
            else racebg = PIXI.Texture.fromFrame(raceKey);

            let streetKey = bginfo.streetbg + ".png";
            if (PIXI.utils.TextureCache[streetKey] === undefined) {
                imgkeys.push({ type: "stree", key: streetKey });
            }
            else streetbg = PIXI.Texture.fromFrame(streetKey);

            let res = await this.AsyncLoadLocalImages(imgkeys);

            for (let i = 0; i < imgkeys.length; i++) {
                switch (imgkeys[i].type) {
                    case "race":
                        racebg = res[imgkeys[i].key].texture;
                        break;
                    case "stree":
                        streetbg = res[imgkeys[i].key].texture;
                        break;
                    default:
                        break;
                }
            }

            this.sendNotification(GameProxyEvent.SETBACKGROUND, { "racebg": racebg, "streetbg": streetbg });
        }
    }

    //
    async setCommercial(urls) {
        //["https://forevertunny.github.io/Image/PK10_BANNER01.jpg" ,"https://forevertunny.github.io/Image/PK10_BANNER02-2.jpg" ]

        let textures = [];
        let urldatas = [];
        for (let i = 0; i < urls.length; i++) {
            let key = urls[i].substring(urls[i].lastIndexOf('/') + 1).replace(/\.[^/.]+$/, "");

            if (key !== "" && this.doesFileExist(urls[i])) {
                if (PIXI.utils.TextureCache[key] === undefined) {
                    urldatas.push({ key: key, url: urls[i] });
                }
                else textures.push(PIXI.Texture.fromFrame(key));
            }
        }

        let res = await this.AsyncLoadImages(urldatas);

        for (let i = 0; i < urldatas.length; i++) {
            textures.push(res[urldatas[i].key].texture);
        }

        this.sendNotification(GameProxyEvent.SETCOMMERCIAL, textures);
    }

    //
    setSeed(seed) {
        this._customRandom.Seed = seed;
        this._isCustSeed = true;
    }

    //
    startCountDown(time) {
        if (this._isStartGame) return;

        this.sendNotification(GameProxyEvent.SHOWTIMER, time);
        this._currCountTimer = time;
        this._isStartCountDown = true;

        if (!this._ticker.started) {
            this._ticker.start();
        }
    }

    //
    midGame() {
        if (!this._isStartGame) {
            if (!this._ticker.started) {
                this._ticker.start();
            }

            this.sendNotification(GameProxyEvent.CLOSETIMER)
            this.startGame();
        }
    }

    //
    finshGame(nums) {
        if (this._isFinshGame) return;

        this.midGame();
        this._finshStartTime = this._ticker.lastTime;

        this._curNum = nums;
        // this._preNum = nums["nums"];
        // this._finshGameCallBack = nums["callback"];

        // this._issue++;
        this.createFinshMove();
        this._currSectionTimer = 0;
        this._currSectionCount = 0;
        this._isFinshGame = true;
    }

    //
    createFinshMove() {
        if (this._curNum.length === 10) {
            this._finshTime = this.createFinshTime();

            let lastXPercent_map = new Map();
            let tempMoveInfoMap_map = new Map();

            //
            for (let i = 0; i < 10; i++) {
                if (i === 0) {
                    lastXPercent_map.push(i + 1, this._customRandom.nextIntRange(GameConfig.MaxMoveXPercent - 5, GameConfig.MaxMoveXPercent + 5));
                }
                else {
                    lastXPercent_map.push(i + 1, this._customRandom.nextIntRange(
                        Math.min(lastXPercent_map.getValue(i) + 4, GameConfig.MinMoveXPercent),
                        lastXPercent_map.getValue(i) + 6));
                }
            }

            //
            for (let i = 0; i < this._objs.length; i++) {
                let tempObj = this._objs.Elements[i].Value;
                let rank = this._curNum.indexOf(tempObj.No + 1) + 1;

                let tempMoveInfo_map;

                for (let x = GameConfig.InsertFinsSection; x > 0; --x) {
                    let tempMoveInfo;

                    if (tempMoveInfoMap_map.containsKey(tempObj.Id)) {
                        tempMoveInfo_map = tempMoveInfoMap_map.getValue(tempObj.Id);
                    }
                    else {
                        tempMoveInfo_map = new Map();
                        tempMoveInfoMap_map.push(tempObj.Id, tempMoveInfo_map)
                    }

                    if (x === GameConfig.InsertFinsSection) {
                        tempMoveInfo = new MoveInfo(this._finshTime[GameConfig.InsertFinsSection - x], lastXPercent_map.getValue(rank));
                        tempMoveInfo.lastSpeed = GameConfig.MoveMaxSpeed - (rank - 1) * rank / GameConfig.MoveMaxSpeed;
                    }
                    else {
                        let disPer = tempMoveInfo_map.getValue(GameConfig.InsertFinsSection).MoveXPer - Math.floor(tempObj.Pos.x / this.StageWidth * 100);
                        let nextPer = disPer / GameConfig.InsertFinsSection;
                        //tempMoveInfo = new MoveInfo(this._finshTime[GameConfig.InsertFinsSection - x], tempMoveInfo_map.getValue(x + 1).MoveXPer - nextPer);
                        tempMoveInfo = new MoveInfo(this._finshTime[GameConfig.InsertFinsSection - x], this.randomMoveInfo(tempObj.No, tempMoveInfo_map.getValue(x + 1).MoveXPer - nextPer).MoveXPer);

                    }
                    tempMoveInfo_map.push(x, tempMoveInfo);
                }
            }

            this._finshMoveInfoMap_map = tempMoveInfoMap_map;
            console.log(tempMoveInfoMap_map);
        }
        else console.error("Err finsh Numbers must be 10 length")
    }

    //
    createFinshTime() {
        let moveTime = GameConfig.FinshTotalTime - GameConfig.RewardTotalTime;
        if (GameConfig.InsertFinsSection < 1) {
            console.error("Error GameConfig InsertFinsSection must be big than 1");
            return null;
        }
        else if (GameConfig.InsertFinsSection === 1) {
            return [GameConfig.FinshTotalTime];
        }
        else {

            // let time = (moveTime - (GameConfig.InsertFinsSection * GameConfig.SectionTime) * 1000) / (GameConfig.InsertFinsSection - 1) / 1000;
            let time = (GameConfig.FinshTotalTime - GameConfig.RewardTotalTime - GameConfig.FlashTime) / (GameConfig.InsertFinsSection + 1) / 1000;

            let tempNumbers = [];
            for (let i = 0; i < GameConfig.InsertFinsSection; i++) {
                // if (i === 0)
                // {
                //     tempNumbers.push(2);
                // }
                // else
                // {
                //     tempNumbers.push(2);
                // }
                tempNumbers.push(time);
            }
            return tempNumbers;
        }
    }

    getBgInfo(key) {
        try {
            if (!isNaN(Number(key))) {
                let index = Number(key);
                if (index < this._bgInfo.length) {
                    return this._bgInfo[index];
                }
            }
            else {
                let tempBginfos = this._bgInfo.filter(s => s.key === key);
                if (tempBginfos.length > 0) {
                    return tempBginfos[0];
                }
            }
        } catch (e) {
            console.error(e)
            console.log(this._bgInfo)
        }
        return null;
    }

    async AsyncLoadImages(url_datas) {
        try {
            return await new Promise((resolve, reject) => {
                let temploader = new PIXI.loaders.Loader();

                for (let i = 0; i < url_datas.length; i++) {
                    if (PIXI.utils.BaseTextureCache[url_datas[i].key] !== undefined) {
                        PIXI.utils.BaseTextureCache[url_datas[i].key].destroy();
                    }
                    temploader.add(url_datas[i].key, url_datas[i].url);
                }

                temploader.onComplete.add((loader, res) => {
                    resolve(res);
                    temploader.destroy();
                });

                temploader.onError.once((loader) => {
                    console.error("AsyncLoadImages Loader Err " + loader);
                })

                temploader.load();
            });
        } catch (e) {
            console.warn(e);
        }
    }

    async AsyncLoadLocalImages(keys) {
        // let baseUrl = document.baseURI;

        try {
            return await new Promise(resolve => {
                let temploader = new PIXI.loaders.Loader();

                for (let i = 0; i < keys.length; i++) {
                    if (PIXI.utils.BaseTextureCache[keys[i].key] !== undefined) {
                        PIXI.utils.BaseTextureCache[keys[i].key].destroy();
                    }
                    temploader.add(keys[i].key, this._assetRootSrc + "assets/" + keys[i].key);
                }

                temploader.onComplete.once((loader, res) => {
                    resolve(res);
                    loader.destroy();
                });

                temploader.onError.once((loader) => {
                    console.error("AsyncLoadLocalImages Loader Err " + loader);
                })
                temploader.load();
            })

        } catch (e) {
            console.warn(e)
        }
    }

    doesFileExist(urlToFile) {
        var xhr = new XMLHttpRequest();
        xhr.open('HEAD', urlToFile, false);
        xhr.send();

        if (xhr.status >= 400) {
            return false;
        } else {
            return true;
        }

    }

    isUrl(str) {
        var pattern = new RegExp('^(https?:\\/\\/)?' + // protocol
            '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.?)+[a-z]{2,}|' + // domain name
            '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
            '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
            '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
            '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
        return pattern.test(str);
    }

    delay(intreval) {
        return new Promise((resolve) => {
            setTimeout(resolve, intreval);
        });
    }

    get IsFinshGame() {
        return this._isFinshGame;
    }

    get StageWidth() {
        return this._stageWidth;
    }

    get StageHeight() {
        return this._stageHeight;
    }

    sendNotification(key, data) {
        if (!this._isRemove) {
            super.sendNotification(key, data)
        }
    }
}
