import * as PIXI from "pixi.js";

export class Reciprocal extends PIXI.Container
{
    /**
     * 
     * @param {PIXI.Texture} startbg 
     * @param {PIXI.Texture} lightbgB 
     * @param {PIXI.Texture} lightbgG 
     * @param {PIXI.Texture} lightbgR 
     */
    constructor(startbg, lightbgB, lightbgG, lightbgR)
    {
        super();

        this._lightBgB = lightbgB;
        this._lightBgG = lightbgG;
        this._lightBgR = lightbgR;

        this._startBg = new PIXI.Sprite(startbg);
        this.addChild(this._startBg);

        this._lightBg1 = new PIXI.Sprite(this._lightBgR);
        this.addChild(this._lightBg1);
        this._lightBg1.x = 5;
        this._lightBg1.y = 2.5;

        this._lightBg2 = new PIXI.Sprite(this._lightBgR);
        this.addChild(this._lightBg2);
        this._lightBg2.x = 55;
        this._lightBg2.y = 2.5;


        this._lightBg3 = new PIXI.Sprite(this._lightBgR);
        this.addChild(this._lightBg3);
        this._lightBg3.x = 105;
        this._lightBg3.y = 2.5;
    }
    reset ()
    {
        this._lightBg1.texture = this._lightBgR;
        this._lightBg2.texture = this._lightBgR;
        this._lightBg3.texture = this._lightBgR;
    }

    uplight (time)
    {
        switch (time)
        {
            case 3:
                this._lightBg1.texture = this._lightBgB;
                break;
            case 2:
                this._lightBg2.texture = this._lightBgB;
                break;
            case 1:
                this._lightBg3.texture = this._lightBgB;
                break;
            case 0:
                this._lightBg1.texture = this._lightBgG;
                this._lightBg2.texture = this._lightBgG;
                this._lightBg3.texture = this._lightBgG;
                break;
            default:
                break;
        }
    }
}