import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { RewardMediator } from "../Mediator/RewardMediator";
import { RewardSprites } from "../Component/RewardSprites";
import { TweenMax } from "gsap/TweenMax";
import { GameCommandEvent } from "../../Event/GameEvent";
import { AudioCommandEvent } from "../../Event/AudioEvent";
import { AudioManifest } from "../../Other/AudioManifest";

export class RewardPanel extends PIXI.Container
{
    // _multitonKey;
    // _stagewidth;
    // _stageheight;

    // _bg;
    // _rd1;
    // _rd2;
    // _rd3;
    // _rewardSpritesComponent;

    constructor(data)
    {
        super();

        this._multitonKey = data.key;
        this._stageWidth = data.width;
        this._stageHeight = data.height;

        this._tweens = [];

        this.once("added", this.onAddToStage);
    }

    onAddToStage ()
    {
        ApplicationFacade.getInstance(this._multitonKey).registerMediator(new RewardMediator(this));
    }

    initRewardPanel (data)
    {
        this.x = this._stageWidth;
        this.visible = true;

        this._bg = new PIXI.Sprite(data["bg"]);
        this._bg.width = this._stageWidth;
        this._bg.height = this._stageHeight;
        this.addChild(this._bg);

        this._rd1 = new PIXI.Sprite(data["rd1"]);
        this._rd1.x = this._stageWidth * 0.5;
        this._rd1.y = this._stageHeight * 0.25;
        this._rd1.anchor.set(0.5);
        this._rd1.scale.set(1.1);
        this._rd1.alpha = 0;

        // this._rd1Flash = new PIXI.Sprite(data["flash"]);
        // this._rd1Flash.x = this._rd1.x;
        // this._rd1Flash.y = this._rd1.y;
        // this._rd1Flash.anchor.set(0.5);
        // this._rd1Flash.scale.set(0.9);
        // this._rd1Flash.alpha = 0;
        // this._rd1Flash.Theta = {
        //     _rotation: 0,
        //     get rotation () { return this._rotation; },
        //     set rotation (v) { this._rotation = v }
        // };
        // this._rd1Flash.Theta.rotation = 1;

        // this.addChild(this._rd1Flash);
        this.addChild(this._rd1);

        this._rd2 = new PIXI.Sprite(data["rd2"]);
        this.addChild(this._rd2);
        this._rd2.x = this._stageWidth * 0.25;
        this._rd2.y = this._stageHeight * 0.3;
        this._rd2.anchor.set(0.5);
        this._rd2.scale.set(1);
        this._rd2.alpha = 0;

        this._rd3 = new PIXI.Sprite(data["rd3"]);
        this.addChild(this._rd3);
        this._rd3.x = this._stageWidth * 0.75;
        this._rd3.y = this._stageHeight * 0.3;
        this._rd3.anchor.set(0.5);
        this._rd3.scale.set(1);
        this._rd3.alpha = 0;

        this._rewardSpritesComponent = new RewardSprites(data["rewardObjTextures"]);
        this.addChild(this._rewardSpritesComponent);
        this._rewardSpritesComponent.createObjSprite();

        // this._rd1Flash_Tween = TweenMax.to(this._rd1Flash.Theta, 2,
        //     {
        //         paused: true,
        //         repeat: -1,
        //         rotation: 360,
        //         ease: Power0.easeNone,
        //         onUpdate: () =>
        //         {
        //             this._rd1Flash.rotation = this.ToRadian(this._rd1Flash.Theta.rotation);
        //         }
        //     });

    }

    reset ()
    {
        for (let i = 0; i < this._tweens.length; i++)
        {
            this._tweens[i].kill();
        }
        this._tweens.length = 0;

        this.visible = false;
        this.alpha = 0;
        this.x = this._stageWidth;

        // this._rd1Flash_Tween.paused(true);
        // this._rd1Flash.Theta.rotation = 0;
        // this._rd1Flash.alpha = 0;

        this._rd1.alpha = 0;
        this._rd2.alpha = 0;
        this._rd3.alpha = 0;

        this._rewardSpritesComponent.reset();
        // ApplicationFacade.getInstance().sendNotification( AudioCommandEvent.STOPAUDIO, "01.all_reward_mp3" );

    }

    showRank (data)
    {
        this._rewardSpritesComponent.ChangeSprite1 = data["first"];
        this._rewardSpritesComponent.ChangeSprite2 = data["second"];
        this._rewardSpritesComponent.ChangeSprite3 = data["third"];

        this.alpha = 0;
        this.visible = true;

        ApplicationFacade.getInstance(this._multitonKey).sendNotification(AudioCommandEvent.PLAY, { "key": AudioManifest.reward });


        TweenMax.to(this, data["rewardTime"] * 0.2 * 0.001,
            {
                alpha: 1,
                x: 0,
                onComplete: () =>
                {
                    setTimeout(() =>
                    {
                        this.showReward(this._rd1, 1);
                        // this.showReward(this._rd1Flash, 1);
                        // this._rd1Flash_Tween.paused(false);
                        // this._rd1Flash_Tween.play(0);
                    }, 50);
                    setTimeout(() => this.showReward(this._rd2, 1), 150);
                    setTimeout(() => this.showReward(this._rd3, 1), 250);

                    setTimeout(() => this.showObj(this._rewardSpritesComponent.Sprite1, 0.3), 900);
                    setTimeout(() => this.showObj(this._rewardSpritesComponent.Sprite2, 0.3), 1200);
                    setTimeout(() => this.showObj(this._rewardSpritesComponent.Sprite3, 0.3), 1500);

                    setTimeout(() =>
                    {
                        ApplicationFacade.getInstance(this._multitonKey).sendNotification(GameCommandEvent.FINSHANIMATION);
                        ApplicationFacade.getInstance(this._multitonKey).sendNotification(GameCommandEvent.RESET);
                    }, data["rewardTime"] * 0.8);
                }
            });

    }

    showReward (obj, delay)
    {
        this._tweens.push(TweenMax.to(obj, delay, { alpha: 1 }));
    }

    showObj (obj, delay)
    {

        let OrignPos = new PIXI.Point(obj.x, obj.y);
        obj.x *= 1.1;
        obj.y *= 0.9;

        this._tweens.push(TweenMax.to(obj, delay,
            {
                alpha: 1,
                x: OrignPos.x,
                y: OrignPos.y
            }));
    }

    ToRadian (degress)
    {
        return degress * (Math.PI / 180);
    }

    get MultitonKey ()
    {
        return this._multitonKey;
    }

    get StageWidth ()
    {
        return this._stageWidth;
    }

    get StageHeight ()
    {
        return this._stageHeight;
    }

}