import MVC from "puremvc";
import { GameProxyEvent } from "../../Event/GameEvent";

export class RewardMediator extends MVC.Mediator
{
    static get NAME ()
    {
        return "RewardMediator";
    }

    constructor(viewComponent)
    {
        super(RewardMediator.NAME, viewComponent);
    }

    listNotificationInterests ()
    {
        return [
            GameProxyEvent.RESET,
            GameProxyEvent.INITREWARDPANEL,
            GameProxyEvent.SHOWRANK,
        ];
    }

    handleNotification (notification)
    {
        switch (notification.getName())
        {
            case GameProxyEvent.RESET:
                this.viewComponent.reset();
                break;
            case GameProxyEvent.INITREWARDPANEL:
                this.viewComponent.initRewardPanel(notification.getBody());
                break;
            case GameProxyEvent.SHOWRANK:
                this.viewComponent.showRank(notification.getBody());
                break;
            default:
                break;
        }
    }
}