import * as PIXI from "pixi.js";
import { TweenMax, Power2 } from "gsap/TweenMax";
import { Timer } from "./Timer";
import { Reciprocal } from "./Reciprocal";

export class TimerGroup extends PIXI.Container
{
    // _timerComponent;

    // _reciprocalComponent;

    init (data)
    {
        this._timerComponent = new Timer(data["standbybg"]);
        this.addChild(this._timerComponent);

        this._reciprocalComponent = new Reciprocal(data["startbg"], data["ligthBbg"], data["lightGbg"], data["ligthRbg"]);
        this._reciprocalComponent.scale.set(0.85);
        this.addChild(this._reciprocalComponent);
        this._reciprocalComponent.x = 310;
        this._reciprocalComponent.y = -200;
        this.visible = false;

    }

    reset ()
    {
        this.visible = false;
        this.alpha = 1;
        this._timerComponent.TimeText.text = "999";

        this._reciprocalComponent.reset();
    }

    show (time, times = 0)
    {
        this.visible = true;
        if (this._timerComponent && this._timerComponent.TimeText)
        {
            this._timerComponent.TimeText.text = "" + time;
        } else if (times < 3)
        {
            setTimeout(this.show.bind(this, time), 100)
        }
    }

    close ()
    {
        TweenMax.to(this, 1, {
            repeat: 0,
            yoyo: false,
            alpha: 0,
            esse: Power2.easeInOut,
            onComplete: () =>
            {
                this.visible = false;
                this.alpha = 1
            }
        });
    }

    upTime (time)
    {
        if (!this._timerComponent) return
        this._timerComponent.TimeText.text = "" + time;

        if (time <= 3)
        {
            this._reciprocalComponent.uplight(time);
        }
    }
}
