import * as PIXI from "pixi.js";
import { GamePanel } from "../View/Panel/GamePanel";
import { RewardPanel } from "../View/Panel/RewardPanel";

export class ApplicationMain extends PIXI.Container
{

    // _gamePanel;
    // _rewardPanel;
    // _data;

    constructor(width, height, key)
    {
        super();

        this.init(width, height, key);
    }

    loadPanel (name)
    {
        switch (name)
        {
            case "Game":
                this._gamePanel = new GamePanel(this._data);
                this.addChild(this._gamePanel);
                this._rewardPanel = new RewardPanel(this._data);
                this.addChild(this._rewardPanel);
                break;
            default:
                break;
        }
    }

    init (width, height, key)
    {
        if (this._gamePanel == null)
        {
            this._gamePanel = new GamePanel({ width: width, height: height, key: key });
            this.addChild(this._gamePanel);
        }

        if (this._rewardPanel == null)
        {
            this._rewardPanel = new RewardPanel({ width: width, height: height, key: key });
            this.addChild(this._rewardPanel);
        }
    }

    get gamePanel ()
    {
        return this._gamePanel
    }

    setData (width, height, key)
    {
        this._data = {
            width: width,
            height: height,
            key: key
        };
    }
}