export class RankInfo
{
    // _no;
    // _posX;
    // _orignX;

    constructor(no, posx, orignx, scale)
    {
        this._no = no;
        this._posX = posx;
        this._orignX = orignx;
        this._scale = scale;
    }

    get No ()
    {
        return this._no;
    }

    get PosX ()
    {
        return this._posX
    }

    get OrignX ()
    {
        return this._orignX;
    }

    get Scale ()
    {
        return this._scale;
    }
}