document.getElementById("setIssue").onclick = () => main.setIssue(document.getElementById("currissue").value, document.getElementById("preissue").value);
document.getElementById("setPreRank").onclick = () => {
    let numSplit = document.getElementById("preRank").value.split(',').map(s => Number(s));
    main.setPreRank(numSplit);
};
document.getElementById("setBackground").onclick = () => main.setBackGround(document.getElementById("background").value);
// document.getElementById("setCommercial").onclick = () =>
// {
//     let splitUrls = document.getElementById("commercial").value.split(",");
//     main.setCommercial(splitUrls)
// };
document.getElementById("setStartCountdown").onclick = () => main.startCountdown(document.getElementById("startCountdown").value);
document.getElementById("midGame").onclick = () => main.midGame();
document.getElementById("setfinishGame").onclick = () => {
    let numSplit = document.getElementById("finishGame").value.split(',').map(s => Number(s));
    main.finishGame(numSplit);
};
document.getElementById("randomFinshTest").onclick = () => main.randomFinshTest();



document.getElementById("removeTest_btn").onclick = (() => {
    main.removeScene();
});


