export class Tool
{
    static RandomRange (min, max)
    {
        return Math.random() * (max - min + 1) + min;
    }


    static Format ()
    {
        var s = arguments[0];
        if (s == null) return "";
        for (var i = 0; i < arguments.length - 1; i++)
        {
            var reg = this.getStringFormatPlaceHolderRegEx(i);
            s = s.replace(reg, (arguments[i + 1] == null ? "" : arguments[i + 1]));
        }
        return this.cleanStringFormatResult(s);
    }

    static PadLeft (str, len, char)
    {
        str = '' + str;
        if (str.length >= len)
        {
            return str;
        }
        else
        {
            return this.PadLeft(char + str, len, char);
        }
    }

    static PadRight (str, len, char)
    {
        str = '' + str;
        if (str.length >= len)
        {
            return str;
        } else
        {
            return this.PadRight(str + char, len, char);
        }
    }

    static getCircle (point, radius = 1, angle = 20)
    {
        let points = [];
        for (let theta = 0; theta <= 360; theta += angle)
        {
            let x = (point.x + radius * Math.cos(-this.ToRadian(theta)));
            let y = (point.y + radius * Math.sin(-this.ToRadian(theta)));

            points.push({ x: x, y: y });
        }

        return points;

    }

    static ToRadian (degress)
    {
        return degress * (Math.PI / 180);
    }

    static getStringFormatPlaceHolderRegEx (placeHolderIndex)
    {
        return new RegExp('({)?\\{' + placeHolderIndex + '\\}(?!})', 'gm')
    }

    static cleanStringFormatResult (txt)
    {
        if (txt == null) return "";
        return txt.replace(this.getStringFormatPlaceHolderRegEx("\\d+"), "");
    }
}