export class MoveInfo
{
    // _time;
    // _moveXPer;
    // _lastSpeed;

    constructor(time, moveXper)
    {
        this._time = time;
        this._moveXPer = moveXper;
    }

    get Time ()
    {
        return this._time;
    }

    set Time (v)
    {
        this._time = v;
    }

    get MoveXPer ()
    {
        return this._moveXPer;
    }

    set MoveXPer (v)
    {
        this._moveXPer = v;
    }

    get lastSpeed ()
    {
        return this._lastSpeed;
    }
    set lastSpeed (v)
    {
        this._lastSpeed = v;
    }

}
