import * as PIXI from "pixi.js";

export class RewardSprites extends PIXI.Container
{
    // _carTextures;

    // _objSprite1;
    // _objSprite2;
    // _objSprite3;

    // _oringPoint1;
    // _oringPoint2;
    // _oringPoint3;

    constructor(textures)
    {
        super();

        this._carTextures = textures;


    }

    createObjSprite ()
    {
        this._objSprite1 = new PIXI.Sprite(this._carTextures[0]);
        this.addChild(this._objSprite1);
        this._objSprite1.x = this.parent.StageWidth * 0.5;
        this._objSprite1.y = this.parent.StageHeight * 0.6;
        this._objSprite1.anchor.set(0.5);
        this._objSprite1.scale.set(1.1);
        this._objSprite1.alpha = 0;

        this._objSprite2 = new PIXI.Sprite(this._carTextures[1]);
        this.addChild(this._objSprite2);
        this._objSprite2.x = this.parent.StageWidth * 0.25;
        this._objSprite2.y = this.parent.StageHeight * 0.6;
        this._objSprite2.anchor.set(0.5);
        this._objSprite2.scale.set(0.55);
        this._objSprite2.alpha = 0;

        this._objSprite3 = new PIXI.Sprite(this._carTextures[2]);
        this.addChild(this._objSprite3);
        this._objSprite3.x = this.parent.StageWidth * 0.75;
        this._objSprite3.y = this.parent.StageHeight * 0.6;
        this._objSprite3.anchor.set(0.5);
        this._objSprite3.scale.set(0.55);
        this._objSprite3.alpha = 0;

        this._oringPoint1 = new PIXI.Point(this._objSprite1.x, this._objSprite1.y);
        this._oringPoint2 = new PIXI.Point(this._objSprite2.x, this._objSprite2.y);
        this._oringPoint3 = new PIXI.Point(this._objSprite3.x, this._objSprite3.y);
    }

    reset ()
    {
        this._objSprite1.alpha = 0;
        this._objSprite2.alpha = 0;
        this._objSprite3.alpha = 0;

        this._objSprite1.x = this._oringPoint1.x;
        this._objSprite1.y = this._oringPoint1.y;

        this._objSprite2.x = this._oringPoint2.x;
        this._objSprite2.y = this._oringPoint2.y;

        this._objSprite3.x = this._oringPoint3.x;
        this._objSprite3.y = this._oringPoint3.y;
    }

    get Sprite1 ()
    {
        return this._objSprite1;
    }

    get Sprite2 ()
    {
        return this._objSprite2;
    }

    get Sprite3 ()
    {
        return this._objSprite3;
    }

    set ChangeSprite1 (no)
    {
        this._objSprite1.texture = this._carTextures[no - 1];
    }

    set ChangeSprite2 (no)
    {
        this._objSprite2.texture = this._carTextures[no - 1];
    }

    set ChangeSprite3 (no)
    {
        this._objSprite3.texture = this._carTextures[no - 1];
    }
}