import MVC from "puremvc";
import { GameProxy } from "../../Mode/Proxy/GameProxy";
import { GameCommandEvent } from "../../Event/GameEvent";

export class GameCommand extends MVC.SimpleCommand {
    static get NAME() {
        return "GameCommand";
    }

    constructor(key) {
        super();

        this.initializeNotifier(key);
    }

    register() {
        let keys = [
            GameCommandEvent.REMOVE,
            GameCommandEvent.INIT,
            GameCommandEvent.INITCOMPLETE,
            GameCommandEvent.RESET,
            GameCommandEvent.SETISSUE,
            GameCommandEvent.SETPRERANK,
            GameCommandEvent.SETREADYCALLBACK,
            GameCommandEvent.SETFINSHLINECALLBACK,
            GameCommandEvent.SETFINSHGAMECALLBACK,
            GameCommandEvent.SETBACKGROUND,
            GameCommandEvent.STARTTIMECOUNTDOWN,
            GameCommandEvent.SETCOMMERCIAL,
            GameCommandEvent.MIDGAME,
            GameCommandEvent.SETSEED,
            GameCommandEvent.FINSHGAME,
            GameCommandEvent.GOAL,
            GameCommandEvent.FLASHLIGHTFINSH,
            GameCommandEvent.FINSHANIMATION,
            GameCommandEvent.CLOSEBTN
        ];

        keys.forEach(key => {
            this.getFacade().registerCommand(key, GameCommand);
        });
    }

    execute(notification) {
        let gameproxy = this.getFacade().retrieveProxy(GameProxy.NAME);

        switch (notification.getName()) {
            case GameCommandEvent.REMOVE:
                gameproxy.remove();
                break;
            case GameCommandEvent.INIT:
                gameproxy.init(notification.getBody());
                break;
            case GameCommandEvent.INITCOMPLETE:
                gameproxy.initcomplete();
                break;
            case GameCommandEvent.RESET:
                gameproxy.reset(notification.getBody());
                break;
            case GameCommandEvent.SETISSUE:
                gameproxy.setIssue(notification.getBody());
                break;
            case GameCommandEvent.SETPRERANK:
                gameproxy.setPreRank(notification.getBody());
                break;
            case GameCommandEvent.SETREADYCALLBACK:
                gameproxy.setReadyCallBack(notification.getBody());
                break;
            case GameCommandEvent.SETRUNSTARTCALLBACK:
                break;
            case GameCommandEvent.SETFINSHLINECALLBACK:
                gameproxy.setFinshLineCallBack(notification.getBody());
                break;
            case GameCommandEvent.SETFINSHGAMECALLBACK:
                gameproxy.setFinshGameCallBack(notification.getBody());
                break;
            case GameCommandEvent.SETSEED:
                gameproxy.setSeed(notification.getBody());
                break;
            case GameCommandEvent.SETBACKGROUND:
                gameproxy.setBackGround(notification.getBody());
                break;
            case GameCommandEvent.STARTTIMECOUNTDOWN:
                gameproxy.startCountDown(notification.getBody());
                break;
            case GameCommandEvent.SETCOMMERCIAL:
                gameproxy.setCommercial(notification.getBody());
                break;
            case GameCommandEvent.MIDGAME:
                gameproxy.midGame();
                break;
            case GameCommandEvent.FINSHGAME:
                gameproxy.finshGame(notification.getBody());
                break;
            case GameCommandEvent.GOAL:
                gameproxy.goal();
                break;
            case GameCommandEvent.FLASHLIGHTFINSH:
                gameproxy.flashLightFinsh();
                break;
            case GameCommandEvent.FINSHANIMATION:
                gameproxy.finshAnimation();
                break;
            case GameCommandEvent.CLOSEBTN:
                gameproxy.onCloseBtn();
                break;
            default:
                break;
        }

    }
}
