import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameCommandEvent } from "../../Event/GameEvent";
import { TweenMax } from "gsap/TweenMax";

export class BackGroundGroup extends PIXI.Container
{

    constructor()
    {
        super();

        this._backgrounds = [];

        this._auditoriums = [];

        this._bgInterval = 0;

        this._auditoriumInterval = 0;

        this._goalLine;
        this._startLine;

        this._goalPosX = 0;
        this._goalHidlePosX = 0;

        this._isBgRun = true;

    }

    reset ()
    {
        for (let i = 0; i < this._backgrounds.length; i++)
        {
            this._backgrounds[i].x = -this._backgrounds[i].width * i;
        }

        for (let i = 0; i < this._auditoriums.length; i++)
        {
            this._auditoriums[i].x = -this._auditoriums[i].width * i;
        }

        this._isBgRun = true;

        this._startLine.alpha = 1;
        this._startLine.x = 528;
        this._startLine.visible = true;

        this._goalLine.visible = false;
        this._goalLine.x = this._goalHidlePosX;
    }

    init (data)
    {
        this._goalPosX = this.parent.StageWidth * data["goalPosXPer"] / 100;
        this._goalHidlePosX = this._goalPosX - this.parent.StageWidth * 1.5;

        this._bgInterval = data["bgInterval"];
        this._auditoriumInterval = data["auditoriumInterval"];

        for (let i = 0; i < data["auditoriumCount"]; i++)
        {
            let tempauditor = new PIXI.Sprite(data["streetbg"]);
            tempauditor.x = -i * tempauditor.texture.width;
            this.addChild(tempauditor);
            this._auditoriums.push(tempauditor);
        }

        for (let i = 0; i < data["bgCount"]; i++)
        {
            let tempBg = new PIXI.Sprite(data["racebg"]);
            tempBg.x = -i * tempBg.texture.width;
            //tempBg.y = 100;
            tempBg.y = this.parent.StageHeight - tempBg.height - 112;
            this.addChild(tempBg);
            this._backgrounds.push(tempBg);
        }

        this._startLine = new PIXI.Sprite(data["goalbg"]);
        this.addChild(this._startLine);
        this._startLine.x = 528;
        this._startLine.y = 120;

        this._goalLine = new PIXI.Sprite(data["goalbg"]);
        this.addChild(this._goalLine);
        this._goalLine.x = this._goalHidlePosX;
        this._goalLine.y = 120;
        this._goalLine.visible = false;
        this._goalLine.anchor.set(0.5, 0);
    }

    ChangeBackGround (data)
    {

        for (let i = 0; i < this._backgrounds.length; i++)
        {
            this._backgrounds[i].texture = data["racebg"];
            this._backgrounds[i].y = this.parent.StageHeight - this._backgrounds[i].height - 112;
        }

        for (let i = 0; i < this._auditoriums.length; i++)
        {
            this._auditoriums[i].texture = data["streetbg"];
        }
    }

    update ()
    {
        if (this._isBgRun)
        {

            for (let i = 0; i < this._backgrounds.length; i++)
            {
                this._backgrounds[i].x += this._bgInterval;

                if (this._backgrounds[i].x > this._backgrounds[i].width)
                {
                    this._backgrounds[i].x -= this._backgrounds[i].width * this._backgrounds.length;
                }
            }


            for (let i = 0; i < this._auditoriums.length; i++)
            {
                this._auditoriums[i].x += this._auditoriumInterval;

                if (this._auditoriums[i].x > this._auditoriums[i].width)
                {
                    this._auditoriums[i].x -= this._auditoriums[i].width * this._auditoriums.length;
                }
            }

            if (this._goalLine != null)
            {
                if (this._goalLine.visible === true)
                {
                    if (Math.round(this._goalLine.x) <= this._goalPosX)
                    {
                        this._goalLine.x += 14.66;
                    }
                    else
                    {
                        this._isBgRun = false;
                        ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(GameCommandEvent.GOAL);
                    }
                }
            }
        }
    }

    openGoal ()
    {
        this._goalLine.visible = true;
    }

    gameStart ()
    {
        TweenMax.to(this._startLine, 1,
            {
                x: this.parent.StageWidth * 1.5,
                alpha: 0,
            });
    }
}
