import * as PIXI from "pixi.js";
import { Obj } from "./Obj";
import { Map } from "../../../Tools/Map";

export class ObjGroup extends PIXI.Container
{
    // _objs;

    constructor()
    {
        super();

        this._objs = new Map();
    }


    init (data)
    {
        for (let i = 0; i < data.length; i++)
        {
            let tempObj = new Obj(data[i]["Id"], data[i]["animaFrame"], data[i]["pos"], data[i]["scale"], data[i]["textures"]);
            this.addChild(tempObj);
            this._objs.push(tempObj.Id, tempObj);
        }
    }

    reset ()
    {
        for (let i = 0; i < this._objs.length; i++)
        {
            this._objs.element(i).Value.reset();
        }
    }

    update (data)
    {
        if (this._objs.containsKey(data["Id"]))
        {
            let tempObj = this._objs.getValue(data["Id"]);
            tempObj.update(data["pos"]);

            if (tempObj.AnimaFrame !== data["animaFrame"])
            {
                tempObj.playAnimation(data["animaFrame"], data["animaFrame"]);
            }
        }
        else console.error("Error Obj_map not have " + data["Id"]);

    }

    animaPause (bool)
    {
        for (let i = 0; i < this._objs.length; i++)
        {
            this._objs.element(i).Value.pause(bool);
        }
    }
}