import * as PIXI from "pixi.js";

export class Timer extends PIXI.Container
{
    // _timeBg;

    // _timeText;

    constructor(timerTexture)
    {
        super();

        this._timeBg = new PIXI.Sprite(timerTexture);
        this.addChild(this._timeBg);
        this._timeBg.anchor.set(0.5, 0.5);

        this._timeText = new PIXI.Text("00000", {
            fontFamily: 'Arial',
            fontSize: 42,
            fontWeight: 'bold',
            fill: ['#ffffff'], // gradient
            stroke: '#6699cc',
            strokeThickness: 5,
            /*
             dropShadow: true,
                        dropShadowColor: '#000000',
                        dropShadowBlur: 4,
                        dropShadowAngle: Math.PI / 6,
                        dropShadowDistance: 6,
                        wordWrap: true,
                        wordWrapWidth: 440
             */

        });
        this.addChild(this._timeText);
        this._timeText.anchor.set(0.5, 0.5);
    }

    get TimeText ()
    {
        return this._timeText;
    }
}

