import * as PIXI from "pixi.js";

export class No extends PIXI.Container
{

    constructor()
    {
        super();

        this._noTextures = [];
        this._noSprites = [];
    }

    init (noTextures)
    {
        this._noTextures = noTextures;

        for (let i = 0; i < noTextures.length; i++)
        {
            let tempSprite = new PIXI.Sprite(noTextures[i]);
            tempSprite.x = i * 50;
            //tempSprite.anchor.set( 0.5, 0 );
            this.addChild(tempSprite);
            this._noSprites.push(tempSprite);
        }

        this.pivot.x = this.width * 0.5;

    }

    reset ()
    {
        for (let i = 0; i < 10; i++)
        {
            this._noSprites[i].texture = this._noTextures[i];
        }
    }

    upRank (ranks)
    {
        if (ranks.length < 10)
        {
            console.error("Error Rank length must be  10");
            return;
        }

        for (let i = 0; i < 10; i++)
        {
            this._noSprites[i].texture = this._noTextures[ranks[i].No];
        }
    }

    upPreRank (ranks)
    {
        if (ranks.length < 10)
        {
            console.error("Error Rank length must be  10");
            return;
        }

        for (let i = 0; i < 10; i++)
        {
            this._noSprites[i].texture = this._noTextures[ranks[i] - 1];
        }
    }

}