import MVC from "puremvc";
import { GameProxyEvent } from "../../Event/GameEvent";

export class GameMediator extends MVC.Mediator
{

    static get NAME ()
    {
        return "GameMediator";
    }

    constructor(viewComponent)
    {
        super(GameMediator.NAME, viewComponent);
    }

    listNotificationInterests ()
    {
        return [
            GameProxyEvent.RESET,
            GameProxyEvent.INITGAMEPANEL,
            // GameProxyEvent.INITNO,
            // GameProxyEvent.INITSOUNDBTN,
            // GameProxyEvent.INITOBJ,
            // GameProxyEvent.INITCOMMERICAL,

            GameProxyEvent.UPISSUE,
            GameProxyEvent.SHOWTIMER,
            GameProxyEvent.UPDATETIME,
            GameProxyEvent.CLOSETIMER,
            GameProxyEvent.UPDATEBACKGROUND,
            GameProxyEvent.SETBACKGROUND,
            GameProxyEvent.OPENGOAL,


            GameProxyEvent.SETCOMMERCIAL,
            GameProxyEvent.GAMESTART,
            GameProxyEvent.UPDATEOBJ,
            GameProxyEvent.UPDATERANK,
            GameProxyEvent.UPDATEPRERANK,
            GameProxyEvent.OBJMOVIECLIPPAUES,
            GameProxyEvent.FlASHLIGHT,
        ];
    }

    handleNotification (notification)
    {

        switch (notification.getName())
        {
            case GameProxyEvent.RESET:
                this.viewComponent.reset();
                break;
            case GameProxyEvent.INITGAMEPANEL:
                this.viewComponent.init(notification.getBody());
                break;
            // case GameProxyEvent.INITNO:
            //     this.viewComponent.NoComponent.initNo(notification.getBody());
            //     break;
            // case GameProxyEvent.INITSOUNDBTN:
            //     this.viewComponent._soundbtnComponent.initSoundBtn(notification.getBody());
            //     break;
            // case GameProxyEvent.INITOBJ:
            //     this.viewComponent.ObjGroup.initObj(notification.getBody());
            //     break;
            // case GameProxyEvent.INITCOMMERICAL:
            //     this.viewComponent.CommercialComponent.initCM(notification.getBody());
            //     break;
            case GameProxyEvent.UPISSUE:
                // this.viewComponent.Issue.upIssue(notification.getBody());
                this.viewComponent.UpIssue(notification.getBody());
                break;
            case GameProxyEvent.SHOWTIMER:
                this.viewComponent.TimerGroupComponent.show(notification.getBody());
                break;
            case GameProxyEvent.UPDATETIME:
                this.viewComponent.TimerGroupComponent.upTime(notification.getBody());
                break;
            case GameProxyEvent.CLOSETIMER:
                this.viewComponent.TimerGroupComponent.close();
                break;
            case GameProxyEvent.UPDATEBACKGROUND:
                this.viewComponent.BackgroundgroupComponent.update();
                break;
            case GameProxyEvent.SETBACKGROUND:
                this.viewComponent.BackgroundgroupComponent.ChangeBackGround(notification.getBody());
                break;

            case GameProxyEvent.OPENGOAL:
                this.viewComponent.BackgroundgroupComponent.openGoal();
                break;


            case GameProxyEvent.SETCOMMERCIAL:
                this.viewComponent.CommercialComponent.setTexture(notification.getBody());
                break;
            case GameProxyEvent.GAMESTART:
                this.viewComponent.gameStart();
                break;
            case GameProxyEvent.UPDATEOBJ:
                this.viewComponent.ObjGroup.update(notification.getBody());
                break;
            case GameProxyEvent.UPDATERANK:
                this.viewComponent.CurrNoComponent.upRank(notification.getBody());
                break;
            case GameProxyEvent.UPDATEPRERANK:
                this.viewComponent.PreNoComponent.upPreRank(notification.getBody());
                break;
            case GameProxyEvent.OBJMOVIECLIPPAUES:
                this.viewComponent.ObjGroup.animaPause(notification.getBody());
                break;
            case GameProxyEvent.FlASHLIGHT:
                this.viewComponent.flashLight(notification.getBody());
                break;
            default:
                break;
        }
    }
}